.head {
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.card-item {
    width: 18rem;
    margin-right: 1rem;
    &:last-child {
        margin-right: 0;
    }
}
.content {
    display: flex;
}
.slide-item {
    margin-top: 5%;
}
.button-item {
    border: 1px solid rgb(179, 179, 179);
}
.button-menu {
    text-align: center;
    margin-top: 5%;
}